/* =============================================================================
RESET
============================================================================= */
body,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, dd, ol, ul,
figure,
hr,
fieldset, legend {
  margin: 0;
  padding: 0; }

img,
svg {
  max-width: 100%;
  height: auto; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

fieldset {
  min-width: 0;
  border: 0; }

/* =============================================================================
SHARED
============================================================================= */
address,
h1, h2, h3, h4, h5, h6,
blockquote, p, pre,
dl, ol, ul,
figure,
hr,
table,
fieldset {
  margin-bottom: 20px; }

dd, ol, ul {
  margin-left: 20px;
  padding-left: 20px; }

/* =============================================================================
BODY
============================================================================= */
body {
  font-size: 16px;
  line-height: 1.625;
  font-family: sans-serif; }

/* =============================================================================
WRAPPER
============================================================================= */
.o-wrapper {
  max-width: 20px;
  margin-left: 0;
  margin-right: 0;
  padding-left: 20px;
  padding-right: 20px; }

/* =============================================================================
BUTTONS
============================================================================= */
/**
 * Clase para la estructura de los botones. Aquí no tienen que ir estilos
 * visuales, solo estructurales, para ellos crea un modificador.
 *
 * 1. Le pasamos el padding
 */
.c-btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: inherit;
  margin: 0;
  cursor: pointer;
  padding: 10px 20px;
  appearance: none;
  background: none;
  border: 0;
  /* [1] */ }
